<template>
    <div page-index>
        <div
            v-shadow="{ radius: 8, width: '80%' }"
            style="display: flex">
            <HeroVue
                name="小舞"
                color="#bd768d">
                <template slot="img">
                    <img
                        :src="`${baseimg}/xw.jpg`"
                        v-img />
                </template>
                <template v-slot:link>
                    <a
                        href="https://wx1.sinaimg.cn/mw2000/006pnshIly1gl3ss2bhgtj31hc0u04ae.jpg"
                        target="_blank"
                        data-link
                        >https://wx1.sinaimg.cn/mw2000/006pnshIly1gl3ss2bhgtj31hc0u04ae.jpg</a
                    >
                </template>
            </HeroVue>
            <HeroVue
                name="上官婉儿"
                color="#2a6e3f">
                <template slot-scope="{ param }">
                    <strong>{{ param }}</strong>
                </template>
            </HeroVue>
        </div>
        <div v-card="500">
            <p>关于插槽</p>
            <div>
                <pre>
动态替换组件内的部分内容
例1: 默认插槽
  &lt;HeroVue name="小舞"&gt;
    &lt;img :src="`${baseimg}/xw.jpg`" /&gt;
  &lt;/HeroVue&gt;
  在使用插槽之前, &lt;herovue&gt;里的元素不会被渲染

  HeroVue组件->
  &lt;div&gt;
    &lt;p&gt;人物名字: { name }&lt;/p&gt;
    &lt;div&gt;
      &lt;p&gt;图片展示区&lt;/p&gt;
      &lt;!-- 图片会显示到此区域 herovue中的内容会完全替换掉slot标签
              当herovue标签内没有内容时, slot标签内的内容会展示 --&gt;
      &lt;slot&gt;我是默认内容&lt;/slot&gt;
    &lt;/div&gt;
  &lt;/div&gt;
例2: 具名插槽
  当组件内需要使用多个slot时, 可以通过给slot标签使用name属性进行区分
  &lt;div&gt;
    &lt;slot name="img"&gt;&lt;/slot&gt;
    &lt;slot name="link"&gt;&lt;/slot&gt;
  &lt;/div&gt;
  &lt;HeroVue&gt;
    &lt;template slot="img"&gt;
      &lt;img src="..." /&gt;
    &lt;/template&gt;
    &lt;template v-slot:link&gt;
      &lt;a href="..." /&gt;
    &lt;/template&gt;
  &lt;/HeroVue&gt;

  在需要展示的元素上使用slot="" or v-slot:xx
例3: 作用域插槽
适用对同一个数据做不同展示
  &lt;slot :param="Naihe"&gt;&lt;/slot&gt;
  &lt;HeroVue&gt;
    &lt;template scope="data"&gt;
      &lt;strong&gt;{data}&lt;/strong&gt;
      &lt;!-- {"param": "Naihe"} --&gt;
      注: 取值使用data["param"]
          也可以在接收时 使用scope="{param}", 取值: {param}
    &lt;/template&gt;
  &lt;/HeroVue&gt;
  注: scope也可以替换为slot-scope
                </pre>
            </div>
        </div>
    </div>
</template>

<script>
import HeroVue from '@/common/Hero.vue';

export default {
    name: 'NSlot',
    components: { HeroVue },
    data() {
        return {};
    },
};
</script>

<style scoped></style>
